.card {
  padding: 65px 44px 65px 30px;
  background-color: #fff;

  &__content {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px 24px 24px 12px;

    & .card__descr {
      line-height: 28px;
    }
  }

  &__title {
    position: relative;
    margin: 0;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 24px;
    line-height: 32px;
    color: #161824;
  }

  &__descr {
    flex-grow: 1;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 32px;
    color: #161824;
  }

  &__link {
    margin-top: auto;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 32px;
    color: #0024ff;
  }

  &.testimonials__item {
    display: flex;
    flex-direction: column;

    &:not(:last-child) {
      margin-right: 36px;
    }
  }

  &__image {
    min-width: 310px;
    height: 200px;
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}