.our-team {
  margin-bottom: 80px;

  .card {
    padding: 0;
    background-color: var(--grey-color);
  }

  .card__image {
    height: auto;
  }

  &__link {
    margin-bottom: 24px;
  }

  &__item {

    &:not(:last-child) {
      margin-right: 36px;
    }
  }

  &__list {
    margin-top: 24px;
    display: flex;
  }

  .card__content {
    padding-left: 0;
    padding-right: 0;
  }

  .card__descr {
    margin: 0;
    font-family: var(--second-family);
    line-height: 31.98px;
    color: #393939;
  }
}