.cases {
  margin-bottom: 80px;

  &__list {
    margin-top: 24px;
    display: flex;
    justify-content: space-between;
  }

  &__item {
    & .card {
      height: 100%;
      display: flex;
      flex-direction: column;
      padding: 0;
      max-width: 310px;
    }

    &:not(:last-child) {
      margin-right: 36px;
    }
  }
}